<template>
    <div class="hot-product" @click="onClick">
        <div class="hot-product-img">
            <img class="auto-img" :src="data.smallImg" :alt="data.name">
            <div class="product-img-hot">{{title}}</div>
        </div>
        <div class="hot-product-info">
            <div class="product-info-name one-text">{{ data.name }}</div>
            <div class="product-info-enname one-text">{{ data.enname }}</div>
            <div class="product-info-price">￥{{ data.price }}</div>
        </div>
    </div>
</template>

<script>
import {toRefs,defineComponent} from 'vue';

export default defineComponent({
    name:'hot-product',
    props: {
        title: String,
        data:{
            type:Object,
            default(){
                return {} 
            }
        }
    },
    setup (props,{emit}) {
        let onClick = () => {
            emit("onClick",props.data)
        }
        return {
            ...toRefs(props),
            onClick
        }
    }
})
</script>

<style lang="less" scoped>
    .hot-product{
        padding: 10px;
        background-color: #fff;
        margin-bottom: 10px;
        border-radius: 8px;
        .hot-product-img{
            border-radius: 8px;
            position: relative;
            overflow: hidden;
            .product-img-hot{
                position: absolute;
                left: 8px;
                top: 0;
                width: 24px;
                text-align: center;
                line-height: 24px;
                height: 24px;
                border-bottom-right-radius: 5px;
                border-bottom-left-radius: 5px;
                background-color: #ff4201;
                color: #fff;
                font-size: 12px;
            }
        }
        .hot-product-info{
            margin-top: 10px;
            .product-info-name{
                margin-bottom: 5px;
            }
            .product-info-enname{
                color: #999;
                font-size: 12px;
                margin-bottom: 10px;
            }
            .product-info-price{
                color: #0C34BA;
                font-weight: bold;
                font-size: 16px;
            }
        }
    }
</style>