import {ref,reactive,toRefs,onMounted,defineComponent} from 'vue';
import {useRouter} from 'vue-router'
import {getRequest} from '../../../request/api';
import url from '../../../request/url';
import HotProduct from '../../../components/HotProduct'

export default defineComponent({
    name:'home',
    components:{
        HotProduct
    },
    setup (props) {
        const value = ref('');
        let state = reactive({
            bannerData:[],
            hotProduct:[],
            userInfo: {}
        });
        let router = useRouter();
        let searchFocus = () => {
            router.push({ name: "search" });
        }

        let getUserInfo = () => {
            getRequest(url.findMy).then(({code,result=[]}) => {
                if(code == 'A001'){
                    let [userInfo] = result
                    Reflect.set(state,'userInfo',userInfo);
                }else{

                }
            }).catch((err) => {
                console.log("err==>",err);
            });
        }

        let banner = () => {
            getRequest(url.banner).then(({code,result = []}) => {
                if(code == 300){
                    Reflect.set(state,'bannerData',result)
                } else{

                }
            }).catch((err) => {
                console.log("err==>",err);
            });
        }
        let typeProducts = () => {
            getRequest(url.typeProducts,{key: "isHot",value: 1}).then(({code,result = []}) => {
                if(code == 500){
                    Reflect.set(state,'hotProduct',result)
                }else{
                
                }
            }).catch((err) => {
                console.log("err==>",err);
            });
        }
        
        let goDetail = (item) => {
            router.push({name:'detail',params:{pid:Reflect.get(item,'pid')}})
        }
        onMounted(() => {
            getUserInfo();
            banner();
            typeProducts();
        })
        return {
            value,
            ...toRefs(state),
            searchFocus,
            goDetail
        }
    }
})