<template>
    <div class="home">
        <van-nav-bar>
            <template #left>
                <div class="home-nav">
                    <div class="home-nav-time">
                        下午好
                    </div>
                    <div class="home-nav-name">
                        {{userInfo.nickName}}
                    </div>
                </div>
            </template>

            <template #right>
                <div class="home-search">
                    <van-search v-model="value" placeholder="输入商品名称" @focus="searchFocus" />
                </div>
            </template>
        </van-nav-bar>
        <div class="home-content">
            <div class="home-content-banner">
                <van-swipe :autoplay="5000" lazy-render>
                    <van-swipe-item v-for="(item,index) in bannerData" :key="index">
                        <img class="auto-img" :src="item.bannerImg" :alt="item.name" @click="goDetail(item)">
                    </van-swipe-item>
                    <template  #indicator="{ active }">
                        <div class="content-banner-pager">
                            <div class="banner-pager-item" :class="{active:active === index}" v-for="(item,index) in bannerData" :key="index">

                            </div>
                        </div>
                    </template>
                </van-swipe>
            </div>

            <div class="home-content-product">
                <div class="content-produc-title">
                    <div class="produc-title-text">
                        <span>热卖推荐</span>
                    </div>
                </div>
            </div>
            <van-row gutter="10">
                <van-col span="12" v-for="(item,index) in hotProduct" :key="index">
                    <hot-product title="hot" :data="item" @onClick="goDetail"></hot-product>
                </van-col>
            </van-row>
        </div>
    </div>
</template>

<script>
    export { default } from './home';
</script>

<style lang="less" scoped>
    @import './home.less';
</style>